<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen :modal="false">
        <el-tabs v-model="activeName">
            <el-tab-pane label="基础信息" name="first">
                <techo-people ref="vBaseForm" :isAdd="isAdd" :dic="dic" @onBasePeople="handleQueryBasePeople" @onPosition="handlePosition"></techo-people>
            </el-tab-pane>
            <el-tab-pane label="位置信息" name="second">
                <vPeoplePosition ref="vPeoplePosition" :xingzhengqu="dic['行政区划']" :disCodeOption="disCodeOption" :isAdd="isAdd"></vPeoplePosition>
            </el-tab-pane>
            <el-tab-pane label="附加信息" name="third">
                <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
                    <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="是否累犯">
                                <el-radio v-model="dialogForm.isRecidivism" :label="1">是</el-radio>
                                <el-radio v-model="dialogForm.isRecidivism" :label="0">否</el-radio>
                            </el-form-item>
                            <el-form-item label="服刑场所">
                                <el-input v-model="dialogForm.serveSentencePlace" :maxlength="25" placeholder="请填写服刑场所"></el-input>
                            </el-form-item> 
                            <el-form-item label="安置日期">
                                <el-date-picker v-model="dialogForm.arrangeDate" type="date" placeholder="请选择日期"></el-date-picker>
                            </el-form-item> 
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="原罪名" prop="beforeCharge">
                                <el-select placeholder="请选择原罪名" clearable v-model="dialogForm.beforeCharge">
                                    <el-option v-for="(item, index) in dic['原罪名']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="危险性评估类型">
                                <el-select placeholder="请选择原罪名" clearable v-model="dialogForm.dangerAssessmentType">
                                    <el-option v-for="(item, index) in dic['危险性评估类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="安置情况">
                                <el-select placeholder="请选择安置情况" clearable v-model="dialogForm.arrangeInfo">
                                    <el-option v-for="(item, index) in dic['安置情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="原判刑期" prop="beforeImprisonment">
                                <el-input v-model="dialogForm.beforeImprisonment" :maxlength="12" placeholder="请填写原判刑期"></el-input>
                            </el-form-item> 
                            <el-form-item label="衔接日期">
                                <el-date-picker v-model="dialogForm.connectDate" type="date" placeholder="请选择日期"></el-date-picker>
                            </el-form-item> 
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="释放日期" prop="releaseDate">
                                <el-date-picker v-model="dialogForm.releaseDate" type="date" placeholder="请选择日期"></el-date-picker>
                            </el-form-item> 
                            <el-form-item label="衔接情况">
                                <el-select placeholder="请选择衔接情况" clearable v-model="dialogForm.connectInfo">
                                    <el-option v-for="(item, index) in dic['衔接情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="12">
                            <el-form-item label="未安置原因">
                                <el-input v-model="dialogForm.noArrangeReason" type="textarea" :rows="3" :maxlength="1024" placeholder="请填写未安置原因"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="帮教情况">
                                <el-input v-model="dialogForm.helpTeachInfo" type="textarea" :rows="3" :maxlength="1024" placeholder="请填写帮教情况"></el-input>
                            </el-form-item>
                        </el-col>
                        </el-row>
                        <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="是否重新犯罪">
                                <el-radio v-model="dialogForm.crimeAgain" :label="1">是</el-radio>
                                <el-radio v-model="dialogForm.crimeAgain" :label="0">否</el-radio>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="重新犯罪罪名" v-if="dialogForm.crimeAgain == 1" prop="newCrimeName">
                                <el-input v-model="dialogForm.newCrimeName" :maxlength="25" placeholder="请填写重新犯罪罪名"></el-input>
                            </el-form-item> 
                        </el-col>
                        <el-col :span="6">
                            
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
			<el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'third'">下一步</el-button>
			<el-button v-if="activeName == 'third'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'third'" type="primary" size="small" @click="activeName = 'second'">上一步</el-button>
            <el-button v-if="activeName == 'third'" type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/special_people_service.js";
import vPeoplePosition from "../infact_peoples/components/PeoplePosition.vue";

export default {
  props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  components: {
    vPeoplePosition
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dic: {
        性别: [],
        民族: [],
        婚姻状况: [],
        政治面貌: [],
        学历: [],
        宗教信仰: [],
        职业类别: [],
        原罪名: [],
        危险性评估类型: [],
        衔接情况: [],
        安置情况: [],
        行政区划: []
      },
      dialogForm: {
        isRecidivism: 0,
        crimeAgain: 0,
        dangerAssessmentType: "02",
        connectInfo: "01",
        arrangeInfo: "02",
      },
      teamRules: {
        beforeCharge: [
          { required: true, message: "请选择原罪名", trigger: "change" }
        ],
        beforeImprisonment: [
          { required: true, message: "请输入原判刑期", trigger: "blur" },
          { min: 2, max: 15, message: "长度在 2 到 15 个字符", trigger: "blur" }
        ],
        releaseDate: [
          { required: true, message: "请选择释放日期", trigger: "change" }
        ],
        newCrimeName: [
          { required: true, message: "请输入原判刑期", trigger: "blur" },
        ],
      },
      houseInfo: {},
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            if (item.name == "职业类别") {
              that.dic[item.name] = that.handleTreeData(data);
            } else {
              that.dic[item.name] = data;
            }
          });
        }
      });
    });
  },

  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["vBaseForm"].baseForm.id &&
          service.requestBaseIdByPeople(
            "people/release/findPage",
            this.$refs["vBaseForm"].baseForm.id,
            data => {
              if (data.totalCount != 0) {
                this.isAdd = false;
                this.dialogForm = data.list[0];
              }
            }
          );
        this.$refs["vBaseForm"].$refs["baseForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
        this.$refs["vPeoplePosition"].$refs["houseItem"] &&
          this.$refs["vPeoplePosition"].$refs["houseItem"].initGridData();
        this.$refs["vPeoplePosition"].$refs["liveHouseItem"] &&
          this.$refs["vPeoplePosition"].$refs["liveHouseItem"].initGridData();
      } else if (name == "third") {
        new Promise((resolve, reject) => {
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            this.$refs["vPeoplePosition"].$refs["houseItem"].$refs[
              "houseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
            this.houseInfo = this.$refs["vPeoplePosition"].$refs[
              "houseItem"
            ].houseInfo;
            resolve();  
          } else {
            service.requestHouseInfo(
              this.$refs["vBaseForm"].baseForm.houseId,
              data => {
                this.houseInfo = data;
                resolve();  
              }
            );
          };
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            this.$refs["vPeoplePosition"].$refs["liveHouseItem"].$refs[
              "liveHouseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
          };
        }).then(() => {
          this.dialogForm = {
            ...this.dialogForm,
            districtCode: this.houseInfo.districtCode
          };
        });
      }
    },
    // 是否重新犯罪
    "dialogForm.crimeAgain"(value) {
        value == 0 && (this.dialogForm.newCrimeName = "");
    },
  },

  
methods: {
    // 职业类型组成树形数据
    handleTreeData(val) {
      // 大类
      let itemONE = _.map(
        _.filter(val, street => {
          return street.code.slice(1, 5) === "0000";
        }),
        street => {
          return {
            code: street.code,
            label: street.name,
            children: []
          };
        }
      );
      // 中类
      _.map(itemONE, item => {
        item.children = _.map(
          _.filter(val, street => {
            return (
              street.code.slice(0, 1) === item.code.slice(0, 1) &&
              street.code.slice(1, 5) !== "0000" &&
              street.code.slice(3, 5) === "00"
            );
          }),
          street => {
            return {
              code: street.code,
              label: street.name,
              children: []
            };
          }
        );
      });
      // 小类
      _.map(itemONE, midItem => {
        _.map(midItem.children, item => {
          item.children = _.map(
            _.filter(val, street => {
              return (
                street.code.slice(0, 3) === item.code.slice(0, 3) &&
                street.code.slice(3, 5) !== "00"
              );
            }),
            street => {
              return {
                code: street.code,
                label: street.name
              };
            }
          );
        });
      });
      return itemONE;
    },
    /** 身份证号选择后回填位置信息 */
    handlePosition(item) {
        this.$refs["vPeoplePosition"].hujidi = item.domicilePlaceDetail;
        this.$refs["vPeoplePosition"].xianzhudi = item.livePlaceDetail;
        this.$refs["vPeoplePosition"].showHujiForm = false;
        this.$refs["vPeoplePosition"].showXianzhuForm = false;
    },
    handleQueryBasePeople(idCardNo, callback) {
      service.requestBasePeopleList(idCardNo, data => {
        callback && callback(data.list);
      });
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let baseInfo = _.cloneDeep(this.$refs["vBaseForm"].baseForm);
          baseInfo.birthDate = baseInfo.birthDate && util.time.formatDate(baseInfo.birthDate,"YYYY-MM-DD");
          baseInfo.occupationType = _.last(baseInfo.occupationType);
          baseInfo.districtCode = this.houseInfo.districtCode;
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            let houseAddress = this.$refs["vPeoplePosition"].$refs["houseItem"]
              .positionForm;
            baseInfo.domicilePlace = houseAddress.province; // 户籍地
            baseInfo.domicilePlaceDetail = houseAddress.domicilePlaceDetail; // 户籍地详址
            baseInfo.houseId = houseAddress.houseId; // 户籍地户ID
          }
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            let liveAddress = this.$refs["vPeoplePosition"].$refs[
              "liveHouseItem"
            ].positionForm;
            baseInfo.livePlace = liveAddress.province; // 居住地
            baseInfo.livePlaceDetail = liveAddress.domicilePlaceDetail; // 居住地详址
            baseInfo.liveHouseId = liveAddress.houseId || ""; // 居住地户ID
          }
          baseInfo.dicMap && delete baseInfo.dicMap;
          if (baseInfo.id) {
            // 修改基本信息
            service.requestUpdatePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          } else {
            // 添加基本信息
            service.requestSavePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    comminForm(data) {
      let otherInfo = _.cloneDeep(this.dialogForm);
      otherInfo.releaseDate = otherInfo.releaseDate && util.time.formatDate(otherInfo.releaseDate,"YYYY-MM-DD");
      otherInfo.connectDate = otherInfo.connectDate && util.time.formatDate(otherInfo.connectDate,"YYYY-MM-DD");
      otherInfo.arrangeDate = otherInfo.arrangeDate && util.time.formatDate(otherInfo.arrangeDate,"YYYY-MM-DD");
      otherInfo.dicMap && delete otherInfo.dicMap;
      if (this.isAdd == true) {
        otherInfo.peopleId = data.id;
        // 添加 社区矫正人员附加信息
        service.requestSaveReleasedPeople(otherInfo, data => {
          this.$popup.showNotification({
            type: "success",
            message: "刑满释放人员添加成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      } else {
        // 修改
        let param = _.pick(otherInfo, [
          "arrangeDate",
          "arrangeInfo",
          "beforeCharge",
          "beforeImprisonment",
          "connectDate",
          "connectInfo",
          "crimeAgain",
          "dangerAssessmentType",
          "districtCode",
          "helpTeachInfo",
          "id",
          "isRecidivism",
          "newCrimeName",
          "noArrangeReason",
          "peopleId",
          "releaseDate",
          "serveSentencePlace"
        ]);
        service.requestUpdateReleasedPeople(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "刑满释放人员修改成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["vBaseForm"].$refs["baseForm"].resetFields();
      this.$refs["vBaseForm"].resetBaseForm();
      this.$refs["dialogForm"].resetFields();
      this.$refs["vPeoplePosition"].destroyedForm();
      this.dialogForm = {
        isRecidivism: 0,
        crimeAgain: 0,
        dangerAssessmentType: "02",
        connectInfo: "01",
        arrangeInfo: "02",
      };
      this.houseInfo = {};
      this.$data.dialogVisible = false;
      this.activeName = "first";
      this.isAdd = true;
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>